/* Reset some default styles */
body, html {
  width: auto; /* Full viewport width */
  max-width: 100vw;
  margin: 0; /* Removes default margin */
  padding: 0; /* Removes default padding */
  scroll-behavior: smooth;
  box-sizing: border-box; /* Ensures padding and borders are included in the total width and height */
  overflow-x: hidden;
 
}

/* Full-height background color for the body */
body {
  background-color: #000000; /* Set background color */
  font-family: Arial, sans-serif; /* Set default font */
  color: #333; /* Set default text color */
  

}


/* Ensure the app container takes full height */
.app-container {
  min-height: 100vh; /* Full viewport height */
  display: flex;
  flex-direction: column;
  overflow-y: auto; /* Enable scrolling on the main content */
  

}

/* Additional styling for the content area */
.content {
  flex: 1; /* Allow content to grow and fill the available space */
  padding: 0; /* Remove padding */

}

/* Section styling */
.section {
  width: 100vw; /* Full viewport width */
  height: 100vh; /* Full viewport height */
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f4f4f4;
  margin: 0; /* Remove margin */
  
}

/* Additional CSS Reset for specific elements if needed */
h1, p, div, span {
  margin: 0; /* Remove default margins */
  padding: 0; /* Remove default padding */
  box-sizing: border-box; /* Apply box-sizing */
}

